import Link from "next/link";

export default function ChineseNavbar() {
  return (
    <nav className="chinese-border bg-black/80 text-[var(--foreground)] py-3 px-0 flex items-center justify-between relative z-20" style={{margin: '32px auto 0', maxWidth: 1200}}>
      {/* 左侧祥云 */}
      <div style={{position: 'absolute', left: 24, top: 8}}>
        <svg width="48" height="24" viewBox="0 0 80 40" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M10 30 Q 20 10, 40 20 T 70 25" stroke="#e7b85a" strokeWidth="2.5" fill="none"/>
        </svg>
      </div>
      <Link href="/" className="text-3xl font-bold tracking-widest" style={{fontFamily: 'var(--font-chinese)'}}>锦绣中华·博文阁</Link>
      <div className="flex gap-8 items-center pr-8">
        <Link href="/" className="px-4 py-1 border-b-2 border-transparent hover:border-[var(--border)] transition-all duration-200 text-xl" style={{fontFamily: 'var(--font-chinese)'}}>首页</Link>
        <Link href="/archive" className="px-4 py-1 border-b-2 border-transparent hover:border-[var(--border)] transition-all duration-200 text-xl" style={{fontFamily: 'var(--font-chinese)'}}>归档</Link>
      </div>
      {/* 右侧祥云 */}
      <div style={{position: 'absolute', right: 24, top: 8}}>
        <svg width="48" height="24" viewBox="0 0 80 40" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M70 10 Q 60 30, 40 20 T 10 15" stroke="#e7b85a" strokeWidth="2.5" fill="none"/>
        </svg>
      </div>
    </nav>
  );
} 