import Image from "next/image";
import Link from "next/link";

export interface ExerciseCardProps {
  id: string | number;
  title: string;
  description: string;
  imageUrl?: string;
  link?: string;
  tags?: string[];
}

export default function ChineseExerciseCard({ title, description, imageUrl, link, tags }: ExerciseCardProps) {
  const cardContent = (
    <>
      {imageUrl && (
        <div className="relative w-full h-44 border-b-2 border-[var(--border)] bg-black/60 flex items-center justify-center">
          <Image
            src={imageUrl}
            alt={title || 'Exercise image'}
            fill
            style={{ objectFit: 'cover', borderRadius: 0, filter: 'grayscale(0.2) contrast(1.1)' }}
            className="transition-transform duration-500 group-hover:scale-105"
          />
          <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-black/0 to-black/0"></div>
        </div>
      )}
      <div className="p-5 flex flex-col flex-grow bg-black/60" style={{fontFamily: 'var(--font-chinese)'}}>
        <h3 className="text-xl font-bold mb-2 text-[var(--foreground)] tracking-widest text-center border-b border-[var(--border)] pb-2" style={{letterSpacing: 2}}>{title}</h3>
        <p className="text-[var(--accent)] text-base mb-4 h-16 overflow-y-auto flex-grow text-center">{description}</p>
        {tags && tags.length > 0 && (
          <div className="mb-4 flex flex-wrap justify-center gap-2">
            {tags.map((tag) => (
              <span
                key={tag}
                className="inline-block bg-black/80 border border-[var(--border)] text-[var(--border)] text-xs font-medium px-3 py-1 rounded-none shadow-sm"
                style={{fontFamily: 'var(--font-chinese)'}}
              >
                {tag}
              </span>
            ))}
          </div>
        )}
        <div className="mt-auto flex justify-center">
          <span className="inline-block px-4 py-1 border border-[var(--border)] text-[var(--border)] bg-black/70 font-bold tracking-widest hover:bg-[var(--border)] hover:text-black transition-colors duration-200 cursor-pointer" style={{fontFamily: 'var(--font-chinese)'}}>
            查看详情
          </span>
        </div>
      </div>
    </>
  );

  return (
    <div className="chinese-border bg-black/70 flex flex-col h-full group transition-transform duration-300 hover:scale-105">
      {link ? (
        <Link href={link} target="_blank" rel="noopener noreferrer" className="flex flex-col h-full">
          {cardContent}
        </Link>
      ) : (
        <div className="flex flex-col h-full">{cardContent}</div>
      )}
    </div>
  );
} 